<template>
  <span class="menu" :class="{open:!!isOpen}" @click="changeState">
    <div class="line"></div>
    <div class="line"></div>
    <div class="line"></div>
  </span>
</template>

<script>
  export default {
    name: "menu-icon",
    data() {
      return {
        isOpen: true
      }
    },
    methods: {
      changeState() {
        this.isOpen = !this.isOpen
        this.$emit('on-state', this.isOpen)
      }
    }
  }
</script>

<style lang="scss" scoped>
  .menu {
    cursor: pointer;
    &:hover {
      opacity: .6;
    }
    &.open {
      .line {
        border-top-color: #a71d5d;
      }
      .line:nth-child(1) {
        transform: rotate(45deg) translateY(5px);
        transform-origin: 5px center;
      }
      .line:nth-child(3) {
        transform: rotate(-45deg) translateY(-5px);
        transform-origin: 5px center;
      }
      .line:nth-child(2) {
        opacity: 0;
        width: 0;
      }
    }
  }

  .line {
    border-top: 2px solid #0086b3;
    width: 20px;
    margin-top: 5px;
    transition: all .5s;
  }
</style>



