import Vue from 'vue'
import Router from 'vue-router'
import JMX from '../views/JMX'
import Home from '../views/Home'
import Category from '../views/Category'
import Archive from '../views/Archive'
import About from '../views/About'
import Tag from '../views/Tag'
import Article from '../views/Article'
import ArticleDetail from '../views/ArticleDetail'
import Meno from '../views/Meno'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/jmx',
    },
    {
      path: '/jmx',
      name: 'jmx',
      component: JMX,
      children: [
        {
          path: 'home',
          name: 'home',
          component: Home,
          children: []
        },
        {
          path: 'category',
          name: 'category',
          component: Category
        },
        {
          path: 'archive',
          name: 'archive',
          component: Archive
        },
        {
          path: 'tag',
          name: 'tag',
          component: Tag
        },
        {
          path: 'about',
          name: 'about',
          component: About
        },
        {
          path: 'article/detail/:id',
          props: (route) => ({id: route.params.id}),
          name: 'article-detail',
          component: ArticleDetail
        },
        {
          path:'meno',
          name: 'meno',
          component:Meno
        }
      ]
    },
    {
      path: '/article',
      name: 'article',
      component: Article
    },
    {
      path: '/article/edit/:id',
      props: (route) => ({id: route.params.id}),
      name: 'article',
      component: Article
    },
    {
      path: '/login',
      name: 'login',
      component: () => import('..//views/Login.vue')
    },
  ]
})



