<template>
  <div class="tag" :style="{color:backgroundColor,fontSize:fontSize}">
    {{text}}<span class="delete" @click="deleteTag">✖</span>
  </div>
</template>

<script>
  export default {
    name: "tag",
    props: {
      text: {
        default: 'tag',
        type: String
      },
      id: {
        type: String || Number,
        required: true
      },
      weight: {
        type: Number,
        default: 1
      }
    },
    computed: {
      backgroundColor() {
        let weight = this.weight
        return `rgb(${255 - 30 * weight},${80 + 10 * weight},${100 + 20 * weight})`
      },
      fontSize() {
        let weight = this.weight
        return `${14 + 2 * weight}px`
      }
    },
    methods: {
      deleteTag() {
        this.$emit('on-delete', this.id)
      }
    },
  }
</script>

<style lang="scss" scoped>
  .delete {
    color: #a71d5d;
    margin-left: 12px;
    font-size: 14px;
    cursor: pointer;
    &:hover {
      opacity: .5;
    }
  }

  .tag {
    display: inline-block;
    padding: 0 10px;
    border: 1px solid #eee;
    background: #ffffff;
    border-radius: 5px;
    height: 40px;
    line-height: 40px;
  }
</style>



