import axios from 'axios'
import sha1 from 'sha1'

const baseUrl = 'https://www.apicloud.com/mcm/api'
const request = axios.create({
  baseURL: baseUrl,
})

const createError = (code, message) => {
  // console.log('-------------------------------', message)
  const err = new Error(message)
  err.code = code
  return err
}
const successResponse = data => {
  return {
    data: data,
    success: true
  }
}
const handleRequest = ({status, data, ...resp}) => {
  // console.log(status, data, resp)
  if (status === 200) {
    let {statusCode, message} = data
    if (statusCode) {
      throw createError(statusCode, message)
    }
    return successResponse(data)
  }
  // if (!data) {
  //   throw createError(400, 'no data')
  // }
  // if (!data.success) {
  //   throw createError(400, 'data.message')
  // }
  /*error: {name: "Error", status: 401, message: "Access Denied", statusCode: 401}
message: "Access Denied"
name: "Error"
status: 401
statusCode: 401*/
  throw createError(status, resp.message)
}

export default (appId, appKey) => {
  const getHeaders = () => {
    // let authorization = localStorage.getItem('ttl_id')
    const Now = Date.now()
    let p = {
      'X-APICloud-AppId': appId,
      'X-APICloud-AppKey': `${sha1(`${appId}UZ${appKey}UZ${Now}`)}.${Now}`,
      // "authorization": authorization || ''
    }
    return p
  }
  return {
    /*登录*/
    async login(params) {
      return handleRequest(await request.post("/user/login", params, {
        headers: getHeaders(),
        'Content-Type': 'application/json'
      }))
    },
    async logout() {
      return handleRequest(await request.post("/user/logout", {}, {
        headers: getHeaders(),
        'Content-Type': 'application/json',
      }))
    },

    async uploadFile(params) {
      return handleRequest(await request.post("/file", params, {
        headers: getHeaders(),
      }))
    },
    /*--------------------------------分类-------------------------------*/
    async getCategories(className) {
      return handleRequest(await
        request.get(`/${className}`, {
          headers: getHeaders(),
          'Content-Type': 'application/json'
        })
      )
    },
    async addCategory(className, params) {
      return handleRequest(await request.post(`/${className}`, params, {
        headers: getHeaders(),
        'Content-Type': 'application/json'
      }))
    },
    async deleteCategory(className, id) {
      return handleRequest(await request.delete(`/${className}/${id}`, {}, {
          headers: getHeaders(),
          'Content-Type':
            'application/json'
        }
        )
      )
    },
    /*--------------------------------标签-------------------------------*/
    async getTags(className) {
      return handleRequest(await
        request.get(`/${className}`, {
          headers: getHeaders(),
          'Content-Type': 'application/json'
        })
      )
    }
    ,
    async addTag(className, params) {
      return handleRequest(await
        request.post(`/${className}`, params, {
          headers: getHeaders(),
          'Content-Type': 'application/json'
        })
      )
    }
    ,
    async deleteTag(className, id) {
      return handleRequest(await
        request.delete(`/${className}/${id}`, {
          headers: getHeaders(),
          'Content-Type': 'application/json'
        })
      )
    }
    ,
    /*-------------------------------文章管理-------------------------------*/
    async getArticles(className) {
      let filter = {
        order: 'createdAt DESC',
      }
      return handleRequest(await
        request.get(`/${className}?filter=${JSON.stringify(filter)}`, {
          headers: getHeaders(),
          'Content-Type': 'application/json'
        })
      )
    }
    ,
    async getArticle(className, id) {
      console.log('-------------getArticle------------------', id)
      return handleRequest(await
        request.get(`/${className}/${id}`, {
          headers: getHeaders(),
          'Content-Type': 'application/json'
        })
      )
    }
    ,
    async updateArticle(className, params) {
      let {id, ...params_} = params
      return handleRequest(await
        request.put(`/${className}/${id}`, params_, {
          headers: getHeaders(),
          'Content-Type': 'application/json'
        })
      )
    }
    ,
    async addArticle(className, params) {
      return handleRequest(await
        request.put(`/${className}`, params, {
          headers: getHeaders(),
          'Content-Type': 'application/json'
        })
      )
    }
    ,
    async deleteArticle(className, id) {
      return handleRequest(await
        request.delete(`/${className}/${id}`, {
          headers: getHeaders(),
          'Content-Type': 'application/json'
        })
      )
    }
    ,
    /*-------------------------便签-------------------------*/
    async getMemos(className) {
      return handleRequest(await
        request.get(`/${className}`, {
          headers: getHeaders(),
          'Content-Type': 'application/json'
        })
      )
    }
    ,
    async addMemo(className, params) {
      return handleRequest(await
        request.post(`/${className}`, params, {
          headers: getHeaders(),
          'Content-Type': 'application/json'
        })
      )
    }
    ,
    async editMemo(className, params) {
      let {id, ...data} = params
      return handleRequest(await
        request.put(`/${className}/${id}`, data, {
          headers: getHeaders(),
          'Content-Type': 'application/json'
        })
      )
    }
  }
}




