module.exports = {
  categories(state, data) {
    state.categories = data
  },
  tags(state, data) {
    state.tags = data
  },
  articles(state, data) {
    state.articles = data
  },
  article(state, data) {
    state.article = data
  },
  memos(state, data) {
    state.memos = data
  },
  memo(state, data) {
    state.memo = data
  },
}



