<template>
  <div class="article-detail container" v-if="article">
    <p class="article-title text-center">{{article.title}}</p>
    <p class="article-info">
      <i class="icon iconfont icon-rili"></i>&nbsp;{{article.createdAt|dateFormat('yyyy-MM-dd')}}&nbsp;&nbsp;|&nbsp;&nbsp;
      <i class="icon iconfont icon-wenjianjia"></i>&nbsp;{{article.category.name}}&nbsp;&nbsp;|&nbsp;&nbsp;
      <i class="icon iconfont icon-biaoqian"></i>&nbsp;{{article.tag.name}}&nbsp;&nbsp;|&nbsp;&nbsp;
      <!--<i class="icon iconfont icon-eye"></i>&nbsp;{{article.see|0}}&nbsp;&nbsp;|&nbsp;&nbsp;-->
      <router-link :to="`/article/edit/${article.id}`"><span class="btn btn-edit">编辑</span></router-link>
    </p>
    <br/>
    <div id="editor">
      <mavon-editor class="markdow-editor" style="height: 100%;z-index: 0;"
                    :value="article.content"
                    :boxShadow="false"
                    :subfield="false"
                    defaultOpen="preview"
                    :editable="false"
                    :toolbarsFlag="false"
      ></mavon-editor>
    </div>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  import {mavonEditor} from 'mavon-editor'
  import 'mavon-editor/dist/css/index.css'

  export default {
    name: "ArticleDetail",
    components: {mavonEditor},
    computed: {
      ...mapGetters({
        article: 'article'
      })
    },
    props: {
      id: {
        type: String,
        required: true
      }
    },
    created() {
      this.$store.dispatch('getArticle', this.id)
    }
  }
</script>

<style lang="scss" scoped>
  .article-title {
    color: #555;
    font-size: 28px;
    margin-top: 30px;
  }

  .article-info {
    text-align: center;
    margin-top: 10px;
    color: #999;
    font-size: 12px;
  }

  #editor {
    color: #777;
    margin-top: 50px;
  }

  .btn-edit {
    color: #0086b3;
  }

  .markdow-editor /deep/ .v-note-panel {
    border: none;
  }
</style>



