<template>
  <div class="dropdown">
    <div class="dropdown-trigger" @click="trigger">
      <slot name="trigger">
        <button class="btn-trigger">{{triggerText}}</button>
      </slot>
      <i class="dropdown-trigger-arrow icon iconfont icon-arrowdown" :class="{up:!this.show}"></i>
    </div>
    <transition name="fade">
      <div class="dropdown-content" :class="{left:alignment==='left'}" v-show="show" :style="styleForContentElm">
        <slot>this is content</slot>
      </div>
    </transition>
  </div>
</template>

<script>
  export default {
    name: "dropdown",
    props: {
      triggerText: {
        type: String,
        default: 'trigger'
      },
      /*dropdown-content部分和dropdown-trigger对齐方式*/
      alignment: {
        type: String,
        default: 'right',//'left'
      },
      /*相隔长度*/
      gapLength: {
        type: Number,
        default: -2
      },
      width: {
        type: Number,
        default: 100
      },
      value: {
        type: Boolean,
        default: false
      }
    },
    watch: {
      value(e) {
        this.show = e
      }
    },
    data() {
      return {
        show: false,
        styleForContentElm: {},
      }
    },
    methods: {
      resizeForContentElm() {
        if (!this.$el)
          return {}
        let contentElm = this.$el.querySelector('.dropdown-trigger')
        if (contentElm) {
          let offset = contentElm.getBoundingClientRect()
          console.log('offset', offset)
          switch (this.alignment) {
            case 'left':
              return {
                left: 0,
                width: this.width + 'px',
                top: (this.gapLength + contentElm.offsetHeight) + 'px',
              }
            default:
              return {
                right: 0,
                top: (this.gapLength + contentElm.offsetHeight) + 'px',
                width: this.width + 'px'
              }
          }
        }
        else return {}
      },
      trigger() {
        // this.show = true
        this.show = !this.show
        this.$emit('input', this.show)
        if (!!this.show)
          this.styleForContentElm = this.resizeForContentElm()
      }
    },
    mounted() {
      console.log('mounted')
    }
  }
</script>

<style lang="scss" scoped>
  .dropdown {
    position: relative;
  }

  .dropdown-content {
    position: absolute;
    padding: 15px;
    background: white;
    border: 1px solid #ededed;
    box-shadow: 1px 1px 10px 0 #ededed;
    z-index: 1;
    &:before {
      content: "";
      position: absolute;
      margin-left: -.5rem;
      top: -.6rem;
      left: 75%;
      width: 1rem;
      height: 1rem;
      background-color: #fff;
      border: 1px solid #ededed;
      border-right: none;
      border-bottom: none;
      transform: rotate(45deg);
    }
    .left {
      &:before {
        left: 25%;
      }
    }
  }

  .btn-trigger {
    background-color: transparent;
    color: #0086b3;
  }

  .dropdown-trigger-arrow {
    color: #0086b3;
    transition: all .5s;
    display: inline-block;
    &.up {
      transform: rotate(180deg);
    }
  }
</style>



