import CATA_API from '../../model/apis/category.requests'
import TAG_API from '../../model/apis/tag.requests'
import ARTICLE_API from '../../model/apis/article.requests'
import MEMO_API from '../../model/apis/memo.requests'

export default {
  //true
  /*-----------------------分类-----------------------------------*/
  async getCategories({commit}) {
    let data = await CATA_API.getCategories()
    commit('categories', data)
  },
  async addCategory({commit, dispatch}, params) {
    await CATA_API.addCategory(params)
    dispatch('getCategories')
  },
  async deleteCategory({commit, dispatch}, id) {
    await CATA_API.deleteCategory(id)
    dispatch('getCategories')
  },
  /*-----------------------------标签-----------------------------*/
  async getTags({commit}) {
    // TAG_API.getTags().then(data => {
    //   commit('tags', data)
    // })
    let data = await TAG_API.getTags()
    commit('tags', data)
    // return Promise.resolve(data) //若组件内需要获取到
  },
  async addTag({commit, dispatch}, params) {
    await TAG_API.addTag(params)
    // console.log(data)
    dispatch('getTags')
  },
  async deleteTag({commit, dispatch}, id) {
    await TAG_API.deleteTag(id)
    // console.log('deleteTag in action:', data)
    dispatch('getTags')
    return 'delete tag ok'
  },
  /*-----------------------------文章-----------------------------*/
  async getArticles({commit}) {
    let data = await ARTICLE_API.getArticles()
    commit('articles', data)
    // return Promise.resolve(data) //若组件内需要获取到
  },
  async getArticle({commit}, id) {
    let data = await ARTICLE_API.getArticle(id)
    commit('article', data)
    return data
  },
  async addArticle({commit, dispatch}, params) {
    await ARTICLE_API.addArticle(params)
    return 'add article ok'
  },
  async updateArticle({commit, dispatch}, params) {
    await ARTICLE_API.updateArticle(params)
    return 'update article ok'
  },
  async deleteArticle({commit, dispatch}, id) {
    await ARTICLE_API.deleteArticle(id)
    dispatch('getArticles')
    return 'delete article ok'
  },
  /*----------------便签-----------------------*/
  async getMemos({commit}) {
    let data = await MEMO_API.getMemos()
    commit('memos', data)
    return Promise.resolve(data) //若组件内需要获取到
  },
  async addMemo({commit, dispatch}, params) {
    await MEMO_API.addMemo(params)
    dispatch('getMemos')
  },
  async editMemo({commit, dispatch}, params) {
    await MEMO_API.editMemo(params)
    dispatch('getMemos')
    return 'edit tag ok'
  },
}



