<template>
  <div class="category">
    <form class="form form-add" @submit="add">
      <h4>
        <center>新增category</center>
      </h4>
      <input class="default" required v-model="category.name" placeholder="输入分类名："/>
      <input class="default" required v-model="category.weight" type="number" min="1" max="6" placeholder="输入权重"/>
      <button class="default" type="submit">add</button>
    </form>
    <br/>
    <hr><br/>
    <div class="text-center category-list">
      <h4>所有分类：</h4><br/>
      <tag class="tag" v-for="item in categories" :key="item.id" :id="item.id" :text="item.name" :weight="item.weight"
           @on-delete="handleDeleteTag"></tag>
    </div>

  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  import Tag from "../layout/tag";

  export default {
    name: "Category",
    components: {Tag},
    computed: {
      ...mapGetters({
        categories: 'categories'
      })
    },
    data() {
      return {
        category: {
          name: '',
          weight: 1,
        }
      }
    },
    methods: {
      add(e) {
        e.preventDefault()
        this.$store.dispatch('addCategory', this.category)
      },
      handleDeleteTag(id) {
        this.$store.dispatch('deleteCategory', id)
      }
    },
    created() {
      this.$store.dispatch('getCategories')
    },
  }
</script>

<style lang="scss" scoped>
  .category-list {
    max-width: 750px;
    .tag {
      margin: auto 5px;
    }
  }
</style>



