import Vuex from 'vuex'
import actions from './actions/actions'
import mutations from './mutations/mutations'
import getters from './getters/getters'
import states from './states/states'
import Vue from 'vue'

Vue.use(Vuex)

export default new Vuex.Store({
  actions: actions,
  getters: getters,
  mutations: mutations,
  state: states
})



