import Confirm from './confirm'
import Vue from "vue";

let instances = []
let seed = 0

const confirmContructor = Vue.extend(Confirm)
export default (options) => {
  let {...propsData} = options
  let instance = new confirmContructor({
    propsData: propsData
  })
  instance.value = true

  instance.vm = instance.$mount()
  instance.vm.$on('input', (e) => {
    instance.value = e
  })
  document.body.appendChild(instance.vm.$el)

  return instance
}



