<template>
  <transition name="fade">
    <div class="modal" v-if="value">
      <div class="shadow"></div>
      <div class="modal-content">
        <slot name="content">
          <slot name="header">
            <div class="header">
              <b>{{title}}</b>
              <i class="icon iconfont icon-cancel btn fr" @click="hideModal"></i>
            </div>
          </slot>
          <slot name="body">
            <div class="body">{{content}}</div>
          </slot>
          <slot name="footer">
            <div class="footer">
              <button @click="cancel" class="btn-cancel" v-if="showCancelBtn">{{cancelText}}</button>
              <button @click="confirm" class="btn-confirm" v-if="showConfirmBtn">{{confirmText}}</button>
            </div>
          </slot>
        </slot>
      </div>
    </div>
  </transition>
</template>

<script>
  export default {
    name: "modal",
    props: {
      value: {
        default: true,
        type: Boolean
      },
      cancelText: {
        default: '取消',
        type: String
      },
      confirmText: {
        default: '确认',
        type: String
      },
      showCancelBtn: {
        default: true,
        type: Boolean
      },
      showConfirmBtn: {
        default: true,
        type: Boolean
      },
      cancelHandler: {
        default: () => {
        },
        type: Function
      },
      confirmHandler: {
        default: () => {
        },
        type: Function
      },
      title: {
        default: 'title',
        type: String
      },
      content: {
        default: 'content',
        type: String
      }
    },
    methods: {
      hideModal() {
        this.$emit('input', false)
      },
      async cancel() {
        if (this.cancelHandler)
          await this.cancelHandler
        this.hideModal()
      },
      async confirm() {
        if (this.confirmHandler)
          await this.confirmHandler()
        this.hideModal()
      }
    },
    mounted() {
      // console.log('modal slots')
    }
  }
</script>

<style lang="scss" scoped>
  .shadow {
    background: rgba(0, 0, 0, .5);
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
  }

  .header, .body, .footer {
    padding: 10px;
  }

  .footer, .body {
    border-top: 1px solid #efefef;
  }

  .footer {
    text-align: right;
  }

  .body {
    min-height: 80px;
  }

  .modal-content {
    background: white;
    min-width: 400px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    border-radius: 5px;
  }


</style>



