<template>
  <transition name="fade" @after-leave="afterLeave">
    <div class="message" v-if="show" :style="{top:verticalOffset+'px'}">
      <i class="iconfont icon" :class="`icon-${iconClassName}`"></i> &nbsp;<span
      class="content">{{content}}</span>
      <!--<button @click="deleteElm">删除</button>-->
    </div>
  </transition>
</template>

<script>
  export default {
    name: "message",
    data() {
      return {
        // type: 'warn',
        show: true,
        id: '',
        timer: null
      }
    },
    props: {
      type: {
        default: 'warn',
        type: String
      },
      content: {
        default: 'content',
        type: String
      },
      duration: {
        default: 3000,
        type: Number
      },
      verticalOffset: {
        default: 80,
        type: Number
      }
    },
    computed: {
      iconClassName() {
        switch (this.type) {
          case 'error':
            return 'cancel'
          case 'success':
            return 'success'
          default :
            return 'warning'
        }
      }
    },
    methods: {
      deleteElm() {
        this.show = false
        this.$emit('close')
      },
      afterLeave() {
        this.$emit('closed')
      },
    },
    mounted() {
      this.timer = setTimeout(() => {
        this.deleteElm()
      }, this.duration)
    },
    destroyed() {
      clearTimeout(this.timer)
    }
  }
</script>

<style lang="scss" scoped>
  .message {
    position: fixed;
    top: 80px;
    left: 50%;
    transform: translateX(-50%);
    transition: all .5s;
    z-index: 100;
  }

  .content {
    font-size: 12px;
  }

  .icon {
    display: inline-block;
    width: 16px;
    height: 16px;
    border-radius: 8px;
    text-align: center;
    color: #fff;
    line-height: 16px;
    font-size: 11px;
  }

  .icon-error {
    background-color: #df0000;
  }

  .icon-warning {
    background-color: #ee9900;
  }

  .icon-success {
    background-color: #00fa0a;
  }

  .icon-cancel {
    color: #a71d5d;
  }
</style>



