export default {
  categories: state => {
    return state.categories
  },
  tags: state => {
    return state.tags
  },
  articles: state => {
    return state.articles
  },
  article: state => {
    return state.article
  },
  memos:state => {
    return state.memos
  },
  memo:state => {
    return state.memo
  },
}



