<template>
  <div>
    <button class="btn-confirm" @click="showDialog(0)">新增</button>
    <div class="">
      <div class="card" v-for="memo in memos" :key="memo.id">
        <p class="article-info" style="color: #777;">
          <i class="icon iconfont icon-rili"></i>&nbsp;{{memo.date|dateFormat('yyyy-MM-dd')}}
          <button class="btn-edit" @click="showDialog(1,memo)">编辑</button>
        </p>
        <br/>
        <p style="color: #555">{{memo.content}}</p>
      </div>
    </div>
    <Modal :title="`${idEdit?'编辑':'新增'}便签`" v-model="show" :cancelHandler="cancelHandler"
           :confirmHandler="confirmHandler">
      <div slot="body" class="body">
        <br/>
        <form class="form">
          <input v-model="memo.date" type="datetime-local" placeholder="输入年月日"/>
          <textarea v-model="memo.content" placeholder="请输入便签内容"></textarea>
        </form>
        <br/>
      </div>
    </Modal>
  </div>
</template>

<script>
  import Modal from "../layout/modal/modal";
  import {mapState} from 'vuex'

  export default {
    name: "Meno",
    components: {Modal},
    computed: {
      ...mapState({
        memos: state => state.memos
      })
    },
    data() {
      return {
        show: false,
        title: '新增便签',
        idEdit: false,
        memo: {
          date: new Date().format('yyyy-MM-dd') + 'T' + new Date().format('hh:mm'),
          content: ''
        }
      }
    },
    methods: {
      cancelHandler() {
        console.log('cancel')
      },
      confirmHandler() {
        console.log('confirm')
        if (!this.idEdit)
          this.add()
        else
          this.edit()
      },
      add() {
        this.$store.dispatch('addMemo', this.memo).then(res => {
          console.log(res)
          if (!res) {
            this.$message({
              type: 'success',
              content: '新增成功!'
            })
          }
        })
      },
      edit() {
        this.$store.dispatch('editMemo', this.memo).then(res => {
          console.log(res)
          if (!res) {
            this.$message({
              type: 'success',
              content: '新增成功!'
            })
          }
        })
      },
      showDialog(type, memo) {
        if (type === 0) {
          this.memo = {
            date: new Date().format('yyyy-MM-dd') + 'T' + new Date().format('hh:mm'),
            content: ''
          }
          this.idEdit = false
        }
        else {
          let date = new Date(memo.date).format('yyyy-MM-dd') + 'T' + new Date(memo.date).format('hh:mm')
          this.memo = {
            date: date,
            content: memo.content,
            id: memo.id
          }
          this.idEdit = true
        }
        this.show = true
        console.log(this.memo)
      }
    },
    created() {
      this.$store.dispatch('getMemos')
    }
  }
</script>

<style scoped>
  .form {
    width: 300px;
    margin: auto;
  }

  textarea {
    width: 300px;
    height: 120px;
  }

  input {
    width: 300px;
    margin-bottom: 10px;
  }

  .body {
    border-top: 1px solid #efefef;
  }

  .card {
    background: #ffffff;
    margin: 20px;
    padding: 20px;
    box-shadow: 1px 1px 10px 0 #ededed;
  }

  .btn-edit {
    background: none;
    color: #0086b3;
    border: 1px solid #0086b3;
    border-radius: 5px;
    float: right;
  }
</style>



