<template>
  <div class="nav-logo">
    <slot></slot>
    <span style="font-size: 20px;color:#777;"><b>Hero's个人博客管理</b></span>
  </div>
</template>

<script>
  import MenuIcon from "../menu-icon";

  export default {
    name: "nav-logo",
    components: {MenuIcon},
    props: {
      hasMenu: {
        default: false,
        type: Boolean
      }
    }
  }
</script>

<style scoped>
  .nav-logo {
    height: 60px;
    line-height: 60px;
  }
</style>



