<template>
  <div class="content-dynamic">
    <div class="container-horizontal" v-if="isHorizontal">
      <div class="bg-white">
        <navbar class="horizontal container">
          <nav-logo class="nav-logo"></nav-logo>
          <div class="clearfix"></div>
        </navbar>
      </div>
      <div class="content">
        <slot></slot>
      </div>
    </div>
    <!--<transition name="fade">-->
    <div class="container-vertical" v-if="!isHorizontal">
      <navbar class="vertical" :class="{closed:!isOpen}">
        <div class="content">
          <nav-logo class="nav-logo">
            <menu-icon class="menu" @on-state="handleMenuState"></menu-icon>
          </nav-logo>
          <slot></slot>
        </div>
      </navbar>
    </div>
    <!--</transition>-->
  </div>
</template>

<script>
  import Navbar from "./navbar";
  import NavLogo from "./nav-logo";
  import MenuIcon from "../menu-icon";

  export default {
    name: "container",
    components: {MenuIcon, NavLogo, Navbar},
    data() {
      return {
        isOpen: true,
        isHorizontal: true
      }
    },
    methods: {
      handleMenuState(e) {
        this.isOpen = e
      },
      resize() {
        let windowHeightLast = 0
        let resizeNav = () => {
          let windowHeight = window.innerWidth
          if (windowHeight > 760) {
            this.isHorizontal = true
          }
          else {
            this.isHorizontal = false
            if (windowHeightLast > 760) {
              if (this.isOpen === false) this.isOpen = true
            }
          }
          windowHeightLast = windowHeight
        }
        resizeNav()
        window.addEventListener('resize', () => {
          resizeNav()
        })
      }
    },
    created() {
      this.resize()
    },
    beforeDestroy() {
      window.removeEventListener('resize', () => {
      })
    }
  }
</script>

<style lang="scss" scoped>
  .content-dynamic, .container-vertical, .navbar-vertical {
    height: 100%;
  }

  .container-vertical {
    display: flex;
  }

  .horizontal {
    .nav-logo {
      float: left;
    }
    /deep/ > .ul {
      float: right;
    }
  }

  .menu {
    position: absolute;
    left: 10px;
    top: 0;
    margin-top: 30px;
    transform: translateY(-50%);
  }

  .bg-white {
    box-shadow: 2px 2px 10px #ddd;
  }

  .vertical {
    /deep/ > ul {
      position: absolute;
      left: 0;
      width: 180px;
      height: 100%;
      background: rgba(0, 134, 179, .1);
      box-shadow: 2px 2px 10px #ddd;
      li {
        padding-left: 40px;
        margin: 0;
        color: #454545;
        font-size: 16px;
        .icon {
          margin-right: 20px;
          font-size: 14px;
        }
      }
    }
    .nav-logo {
      background: #ffffff;
      box-shadow: 2px 2px 10px #ddd;
      padding-left: 10px;
      text-align: center;
    }
    > .content {
      flex-grow: 1;
      position: absolute;
      left: 180px;
      right: 0;
      transition: all .5s;
      min-height: 100%;
      background: #fafafa;
    }
    &.closed {
      .content {
        left: 0;
      }
    }
  }

  /*@media screen and (min-width: 760px) {*/
  /*.container-horizontal {*/
  /*display: block;*/
  /*}*/

  /*.container-vertical {*/
  /*display: none;*/
  /*}*/
  /*}*/

  /*@media screen and (max-width: 760px) {*/
  /*.container-horizontal {*/
  /*display: none;*/
  /*}*/

  /*.container-vertical {*/
  /*display: block;*/
  /*}*/
  /*}*/
</style>



