<template>
  <div class="home container">
    <div class="article-panel" v-for="article in articles" :key="article.id">
      <p class="article-title">{{article.title}}</p>
      <p class="article-info">
        <i class="icon iconfont icon-rili"></i>&nbsp;{{article.createdAt|dateFormat('yyyy-MM-dd')}}&nbsp;&nbsp;|&nbsp;&nbsp;
        <i class="icon iconfont icon-wenjianjia"></i>&nbsp;{{article.category.name}}&nbsp;&nbsp;|&nbsp;&nbsp;
        <i class="icon iconfont icon-biaoqian"></i>&nbsp;{{article.tag.name}}&nbsp;&nbsp;|&nbsp;&nbsp;
        <!--<i class="icon iconfont icon-eye"></i>&nbsp;{{article.see|0}}-->
      </p>
      <p class="article-content">
        {{article.content}}
      </p>
      <router-link :to="`/jmx/article/detail/${article.id}`">
        <div class="btn btn-see-all">阅读全文<span>>>></span></div>
      </router-link>
      &nbsp;<span style="color: #d1d1d1;font-size: 12px">|</span>&nbsp;
      <span class="btn btn-cancel" @click="deleteArticle(article.id)">删除</span>
    </div>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'

  export default {
    name: "Home",
    computed: {
      ...mapGetters({
        articles: 'articles',
      })
    },
    methods: {
      deleteArticle(id) {
        this.$confirm({
          content: '确定要删除该文章吗？',
          confirmHandler: () => {
            return this.confirmHandler(id)
          }
        })
      },
      async confirmHandler(id) {
        return await this.$store.dispatch('deleteArticle', id)
      }
    },
    beforeCreate() {
      this.$store.dispatch('getArticles')
    }
  }
</script>

<style lang="scss" scoped>
  .article-title {
    color: #555;
    font-size: 28px;
  }

  .article-info {
    display: flex;
    align-items: center;
    margin-top: 10px;
    color: #999;
    font-size: 12px;
  }

  .article-content {
    color: #777;
    margin-top: 16px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
  }

  .article-panel {
    margin-top: 100px;
    &:first-child {
      margin-top: 0;
    }
  }

  .btn-see-all {
    color: #666;
    margin-top: 24px;
    font-size: 14px;
    color: #0086b3;
    display: inline-block;
    border-bottom: 1px solid #0086b3;
    padding-bottom: 5px;
    span {
      font-size: 10px;
      vertical-align: middle;
    }
  }

  .btn-cancel {
    color: #a71d5d;
    font-size: 14px;
  }
</style>



