<template>
  <div class="tag">
    <form class="form form-add" @submit="add">
      <h4>
        <center>新增标签</center>
      </h4>
      <input class="default" required v-model="tag.name" placeholder="输入标签名："/>
      <input class="default" required v-model="tag.weight" type="number" min="1" max="6" placeholder="输入权重"/>
      <button class="default" type="submit">add</button>
    </form>
    <br/>
    <hr>
    <br/>
    <div class="text-center category-list">
      <h4>所有标签：</h4><br/>
      <tag_ class="tag" v-for="item in tags" :key="item.id" :id="item.id" :text="item.name" :weight="item.weight"
            @on-delete="handleDeleteTag"></tag_>
    </div>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  import Tag from "../layout/tag";
  import Message from "../layout/message/message";

  export default {
    name: "Tag",
    components: {Message, Tag_: Tag},
    computed: {
      ...mapGetters({
        tags: 'tags'
      })
    },
    data() {
      return {
        showModal: true,
        tag: {
          name: '',
          weight: 1,
        }
      }
    },
    methods: {
      add(e) {
        e.preventDefault()
        this.$store.dispatch('addTag', this.tag)
      },
      handleDeleteTag(id) {
        this.$confirm({
          content: '确定要删除该标签吗？',
          confirmHandler: () => {
            return this.confirmHandler(id)
          }
        })
      },
      async confirmHandler(id) {
        return await this.$store.dispatch('deleteTag', id)
      }
    },
    created() {
      this.$store.dispatch('getTags')
    },
  }
</script>

<style lang="scss" scoped>
  .category-list {
    max-width: 750px;
    .tag {
      margin: auto 5px;
    }
  }
</style>



