import createDb from '../apicloud.db'
import config from '../apicloud.db.conf'

const db = createDb(config.appId, config.appKey)
import handleRequest from '../utils'

const className = 'memo'
export default {
  async getMemos() {
    return await handleRequest(db.getMemos(className))
  },
  async addMemo(params) {
    return await handleRequest(db.addMemo(className, params))
  },
  async editMemo(params) {
    return await handleRequest(db.editMemo(className, params))
  },
}



