<template>
  <div class="nav-bar">
    <transition fade>
      <ul v-if="show" class="ul">
        <router-link to="/jmx/home">
          <li><i class="icon iconfont icon-home"></i> 首页</li>
        </router-link>
        <router-link to="/jmx/meno">
          <li :class="{active:$route.name==='meno'}"><i class="icon iconfont icon-MemoFilled"></i> 工作总结</li>
        </router-link>
        <router-link to="/jmx/category">
          <li :class="{active:$route.name==='category'}"><i class="icon iconfont icon-categoryselected"></i> 分类</li>
        </router-link>
        <router-link to="/jmx/tag">
          <li :class="{active:$route.name==='tag'}"><i class="icon iconfont icon-tag"></i> 标签</li>
        </router-link>
        <router-link to="/jmx/archive">
          <li :class="{active:$route.name==='archive'}"><i class="icon iconfont icon-archive"></i> 归档</li>
        </router-link>
        <router-link to="/jmx/about">
          <li :class="{active:$route.name==='about'}"><i class="icon iconfont icon-guanyuwo"></i> 关于</li>
        </router-link>
        <router-link to="/article">
          <li class="publish btn" style="color: #0086b3;"><i class="icon iconfont icon-guanyuwo"
                                                             style="opacity: 0;"></i> 发布
          </li>
        </router-link>
        <li class="publish btn" style="color: #0086b3;" @click="logout"><i class="icon iconfont icon-guanyuwo"
                                                                           style="opacity: 0;"></i> 退出
        </li>
      </ul>
    </transition>
    <slot></slot>
  </div>
</template>

<script>
  import API from '../../model/apis/user.requests'

  export default {
    name: "navbar",
    props: {
      show: {
        default: true,
        type: Boolean
      }
    },
    methods: {
      logout() {
        API.logout({username: 'jmx'}).then(res => {
          if (res) {
            if (res.status != 0)
              this.$router.push('/login')
          }
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  .icon {
    font-size: 12px;
    margin-right: 5px;
  }

  .logo {
    height: 60px;
    line-height: 60px;
    margin: auto 20px;
  }

  .nav-bar {
    li {
      display: inline-block;
      height: 60px;
      line-height: 60px;
      padding: 0 10px;
      &.publish {
        padding: 0;
      }
      &:hover {
        background: #0086b3;
        color: #fff;
        &.publish {
          background: none;
          color: #0086b3;
        }
      }
      &.active {
        background: #0086b3;
        color: #fff;
      }
    }
  }

  /*默认样式*/
  .nav-bar.vertical {
    height: 100%;
    li {
      display: block;
    }
  }
</style>



