import createDb from '../apicloud.db'
import config from '../apicloud.db.conf'

const db = createDb(config.appId, config.appKey)
import handleRequest from '../utils'

const className = 'user'
export default {
  async login(params) {
    // console.log(`user is in request : ${params}`)
    return await handleRequest(db.login(params))
  },
  async logout(params) {
    // console.log(`user is in request : ${params}`)
    return await handleRequest(db.logout(params))
  },
}



