import createDb from '../apicloud.db'
import config from '../apicloud.db.conf'

const db = createDb(config.appId, config.appKey)
import handleRequest from '../utils'

const className = 'article'
export default {
  async getArticles() {
    return await handleRequest(db.getArticles(className))
  },
  async getArticle(id) {
    return await handleRequest(db.getArticle(className, id))
  },
  async addArticle(params) {
    return await handleRequest(db.addArticle(className, params))
  },
  async updateArticle(params) {
    return await handleRequest(db.updateArticle(className, params))
  },
  async deleteArticle(id) {
    return await handleRequest(db.deleteArticle(className, id))
  },
  // async articleView(id) {
  //   return await handleRequest(db.viewArticle(className, id, 'view'))
  // }
}



