import Message from './message.vue'
import Vue from 'vue'

const instances = []
let seed = 0  //id自增长

/*从instances里面将实例删除*/
function removeInstance(instance) {
  if (!instance) return
  let len = instances.length
  const index = instances.findIndex(item => {
    return instance.id === item.id
  })
  instances.splice(index, 1)
  const rmHeight = instance.height
  for (let i = index; i < len - 1; i++) {
    // console.log(`before:${instances[i].verticalOffset},rmHeight：${rmHeight},下移：${rmHeight + 16}`)
    let offset = parseInt(instances[i].verticalOffset - rmHeight - 16)
    instances[i].verticalOffset = offset
    // console.log(`after:${instances[i].verticalOffset}`)
  }
}

const messageConstructor = Vue.extend(Message)


const message = (options) => {
  let instance
  if (typeof options === 'string') {
    let content = options
    options = {
      content: content
    }
  }
  let {...propsData} = options
  instance = new messageConstructor({
    propsData: propsData,//{...propsData}也可以
  })
  seed++;
  instance.id = seed
  instance.vm = instance.$mount()//el已经生成，但是没有插入到节点上
  document.body.appendChild(instance.vm.$el)
  /*必须在mounted及插入到body后才能获得offsetHeight*/
  instance.height = instance.$el.offsetHeight//记录实际高度

  let verticalOffset = 80
  instances.forEach(item => {
    verticalOffset += item.$el.offsetHeight + 16
  })
  verticalOffset += 16
  instance.verticalOffset = verticalOffset
  instances.push(instance)

  /*将要关闭*/
  instance.vm.$on('close', () => {
    console.log('on close')
    instance.vm.$data.show = false
  })
  /*已关闭*/
  instance.vm.$on('closed', () => {
    console.log('on closed')
    removeInstance(instance)
    document.body.removeChild(instance.vm.$el)
    instance.vm.$destroy() // 销毁
  })
  return instance
}

export default message





