<template>
  <div class="article">
    <div class="article-header" style="display: flex;align-items: center;justify-content: space-between;">
      <input class="input" v-model="article.title" placeholder="输入文章标题..."/>
      <dropdown class="drop-down" style="margin-right: 15px"
                :width="400"
                alignment="right"
                v-model="showMenu"
                :triggerText="triggerText">
        <div class="inner-content">
          <h4 style="color: hsla(218,9%,51%,.8)">&nbsp;选择文章标签</h4><br/>
          <div class="input-radio" v-for="item in tags" :class="{checked:(item.id===article['tag(uz*R*id)'])}">
            <label>{{item.name}}</label>
            <input class="btn" type="radio"
                   :key="item.id"
                   :value="item.id"
                   v-model="article['tag(uz*R*id)']"/>
          </div>
          <hr>
          <h4 style="color: hsla(218,9%,51%,.8);margin-top: 15px">&nbsp;选择文章分类</h4><br/>
          <div class="input-radio" v-for="(item) in categories"
               :class="{checked:(item.id===article['category(uz*R*id)'])}">
            <label>{{item.name}}</label>
            <input class="btn" type="radio"
                   :key="item.id"
                   :value="item.id"
                   v-model="article['category(uz*R*id)']"/>
          </div>
          <hr>
          <h4 style="color: hsla(218,9%,51%,.8);margin-top: 15px">&nbsp;选择文章公开性</h4><br/>
          <select v-model="article.show" class="select-show">
            <option name="true" value="true">公开</option>
            <option name="false" value="false">私密</option>
          </select>
          <hr>
          <div class="text-center">&nbsp;<button class="btn-confirm" @click="confirmSubmit">确定并{{triggerText}}
          </button>
          </div>
        </div>
      </dropdown>
    </div>
    <div id="editor">
      <mavon-editor ref="md" @imgAdd="imgAdd" class="markdow-editor" style="height: 100%;z-index: 0;"
                    v-model="article.content"></mavon-editor>
    </div>
  </div>
</template>

<script>
  import {mavonEditor} from 'mavon-editor'
  import 'mavon-editor/dist/css/index.css'
  import dropdown from "../layout/dropdown";
  import {mapGetters} from 'vuex'
  import FILE_API from '../model/apis/file.request'

  export default {
    name: "Article",
    components: {
      dropdown,
      mavonEditor
    },
    computed: {
      ...mapGetters({
        categories: 'categories',
        tags: 'tags',
      }),
      triggerText() {
        return this.id ? '更新' : '发布'
      }
    },
    props: {
      id: {
        type: String
      }
    },
    data() {
      return {
        article: {
          title: 'aaaaaaa',
          "tag(uz*R*id)": '',//id
          "category(uz*R*id)": '',//id
          content: '',
          view: 0,
          show: true,
        },
        showMenu: false
      }
    },
    methods: {
      getTagById(id) {
        return this.tags.find(item => {
          return item.id === id
        })
      },
      getCategoryById(id) {
        return this.categories.find(item => {
          return item.id === id
        })
      },
      confirmSubmit() {
        let hint = ''
        if (this.article.title === '')
          hint = '请输入文章标题'
        else if (this.article.content === '')
          hint = '请编辑文章内容'
        else if (this.article.tag === '')
          hint = '请选择标签'
        else if (this.article.category === '')
          hint = '请选择分类'
        if (hint !== '') {
          this.$message(hint)
          return
        }
        this.showMenu = false
        if (!this.id) {
          return this.newArticle()
        }
        return this.updateArticle()
        s
      },
      newArticle() {
        let params = this.article
        params.id = this.id
        params.category = this.getCategoryById(this.article['category(uz*R*id)'])
        params.tag = this.getTagById(this.article['tag(uz*R*id)'])
        params.view = 0
        /*创建文章，自动向tag和catagory表中插入关联  tag-article是一对多的关系*/

        this.$store.dispatch('addArticle', params).then(res => {
          if (!!res) {
            this.$message({
              type: 'success',
              content: '创建成功!'
            })
            setTimeout(() => {
              this.$router.replace('/jmx/home')
            }, 3000)
          }
        })
      },
      updateArticle() {
        let params = this.article
        params.category = this.getCategoryById(this.article['category(uz*R*id)'])
        params.tag = this.getTagById(this.article['tag(uz*R*id)'])
        this.$store.dispatch('updateArticle', params).then(res => {
          if (!!res) {
            this.$message({
              type: 'success',
              content: '更新成功!'
            })
            setTimeout(() => {
              this.$router.replace('/jmx/home')
            }, 3000)
          }
        })
      },
      imgAdd(pos, file) {
        console.log(pos, file)
        let formdata = new FormData();
        let dataBase64 = file.miniurl
        let filename = file.name

        formdata.append("file", file, filename);
        console.log('imgFile:', formdata)
        FILE_API.uploadFile(formdata).then(res => {
          if (res)
            this.$refs.md.$img2Url(pos, res.url)
        })
      },
    },
    created() {
      this.$store.dispatch('getCategories')
      this.$store.dispatch('getTags')
      console.log('----------', this.id)
      if (!!this.id) {
        this.$store.dispatch('getArticle', this.id).then(res => {
          if (res)
            this.article = res
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  .article {
    height: 100%;
  }

  #editor {
    flex-grow: 1;
    position: absolute;
    top: 60px;
    left: 0;
    right: 0;
    bottom: 0;
    .markdow-editor /deep/ .v-note-op.shadow {
      box-shadow: 0 0px 1px rgba(0, 0, 0, 0.157), 0 0px 1px rgba(0, 0, 0, 0.227);
    }
  }

  .article-header {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    background: #ffffff;
    border-bottom: 1px solid #ededed;
    height: 60px;
    line-height: 60px;
    input {
      border: none;
      height: 40px;
      font-size: 18px;
      font-weight: bold;
      padding: 0 15px;
      flex: 1;
      &::placeholder {
        color: #777;
      }
    }
  }

  .input-radio {
    position: relative;
    display: inline-block;
    height: 30px;
    line-height: 30px;
    margin: 5px;
    padding: 0 5px;
    border: 1px solid #efefef;
    &:hover, &.checked {
      background: rgba(0, 128, 200, .1);
      border: 1px solid rgba(0, 128, 200, .1);
      > label {
        color: #0086b3;
      }
    }
    input[type=radio] {
      opacity: 0;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      display: block;
    }
    label {
      color: #777;
    }
  }

  .inner-content {
    line-height: initial;
  }

  .select-category.placeholder {
    color: hsla(218, 9%, 51%, .8);
    height: 60px;
    border: none;
    float: right;
  }

  .markdown_ /deep/ .markdown-editor > textarea {
    background: #f1f1f1 !important;
  }

  .markdown_ /deep/ .markdown-editor {
    background: #f1f1f1 !important;
  }

  .markdown_ /deep/ .empty {
    width: 0 !important;
  }

  .select-show {
    height: 30px;
    line-height: 30px;
    margin: 5px;
    padding: 0 5px;
    border: 1px solid #efefef;
  }
</style>



